import { getClientes, getClientesActive } from "../../services/clienteService";
import { useEffect, useState } from "react";
import useClientes from "../../hooks/useClientes";

const hojaVacia = {
  avance: 0,
  total: 0,
};

export const usePedidos = () => {
  const { clientes } = useClientes(true);
  const [hojaPedido, setHojaPedido] = useState({ ...hojaVacia });
  const [pedidos, setPedidos] = useState([]);

  useEffect(() => {
    if (localStorage.getItem("hojaPedido")) {
      setHojaPedido({ ...hojaPedido, ...JSON.parse(localStorage.getItem("hojaPedido")) });
    }
    if (localStorage.getItem("pedidos")) {
      setPedidos([...pedidos, ...JSON.parse(localStorage.getItem("pedidos"))]);
    }
  }, []);

  const handlePedido = (i, e) => {
    if(e.target.value >= 0) {
      let pedidoNuevo, aux;
      pedidoNuevo = e.target.value * 1;
      aux = pedidoNuevo;
      if (pedidos[i]) {
        aux -= pedidos[i];
      }
      if (hojaPedido.total - (hojaPedido.avance + aux) >= 0) {
        pedidos[i] = pedidoNuevo;
        localStorage.setItem("pedidos", JSON.stringify(pedidos));
        setHojaPedido({ ...hojaPedido, avance: hojaPedido.avance + aux });
        localStorage.setItem(
          "hojaPedido",
          JSON.stringify({ ...hojaPedido, avance: hojaPedido.avance + aux })
        );
      }
    }
  };

  const handleTotal = (e) => {
    setHojaPedido({ ...hojaPedido, total: (+e.target.value) });
    localStorage.setItem(
      "hojaPedido",
      JSON.stringify({...hojaPedido, total: (+e.target.value) })
    );
  };

  const limpiarHoja = () => {
    localStorage.removeItem("hojaPedido");
    localStorage.removeItem("pedidos");
    setHojaPedido({...hojaVacia})
    setPedidos([])
  };

  return {
    hojaPedido,
    pedidos,
    clientes,
    handlePedido,
    handleTotal,
    limpiarHoja,
  };
};
