import {
  Table,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TextField,
  TableBody,
  Button,
} from "@material-ui/core";
import React from "react";
import { usePedidos } from "./hooks";
import { useStyles } from "./styles";

const HojaPedido = () => {
  const { hojaPedido, pedidos, clientes, handlePedido, handleTotal, limpiarHoja } =
    usePedidos();

  const classes = useStyles()

  return (
    <div>
      <div className={classes.containerButtons}>
        <TextField
          label="Total"
          value={hojaPedido.total !== 0 ? hojaPedido.total : ""}
          placeholder="0"
          onChange={handleTotal}
          type="number"
        />
        <TextField label="Queda" value={hojaPedido.total !== 0 ? (hojaPedido.total - hojaPedido.avance) : ""} type="number" />
        <Button variant="contained" color="primary" onClick={limpiarHoja}>
          Limpiar Hoja
        </Button>
      </div>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              {["Clientes", "NÂ° de jabas"].map((cellHead) => (
                <TableCell key={cellHead}>{cellHead}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {clientes.map((objCliente, i) => (
              <TableRow key={i}>
                <TableCell className={classes.cellBody}>{objCliente.nombre}</TableCell>
                <TableCell className={classes.inputCell}>
                  <TextField
                    size="small"
                    type="number"
                    variant="filled"
                    value={(pedidos[i] === undefined) ? "" : pedidos[i]}
                    onChange={(e) => {
                      handlePedido(i, e);
                    }}
                  />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default HojaPedido;
